#siteContainer {
  width: 100vw;
}

#box1 {
  width: 100%;
  height: 65vh;
  background-color: rgb(214, 231, 223);
}

#navBar {
  /* background-color: rgba(220, 20, 60, 0.445); */
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 22px 0px;
}

#danaKenney {
  font-size: 20px;
  color: #538d72;
  margin: 0px;
  font-weight: 500;
  margin: 0px 0px 0px 33px;
}

.navMiddleItem {
  margin: 0px 10px;
  font-size: 13px;
  font-weight: 500;
}

#contactButton {
  border: none;
  outline: none;
  background-color: #538d72;
  color: white;
  font-size: 10px;
  padding: 10px 18px;
  cursor: pointer;
  margin: 0px 33px 0px 0px;
}

#contactButton:hover {
  background-color: #7fa895;
}

#sloganContainer {
  width: 36vw;
  /* background-color: rgba(220, 20, 60, 0.39); */
  margin: 125px 0px 0px 0px;
}

#slogan {
  color: #538d72;
  text-align: center;
  font-weight: 500;
  font-size: 40px;
  margin: 0px;
}

#consultation1 {
  margin: 32px 0px;
  color: #538d72;
  font-style: italic;
  text-decoration: underline;
  font-size: 18px;
}

#box2 {
  background-color: white;
  width: 100%;
  height: 30vh;
  justify-content: center;
}

#quote1Container {
  width: 42vw;
}

#quote1 {
  margin: 0;
  text-align: center;
  font-weight: 500;
  font-size: 20px;
  line-height: 36px;
}

#mainPhoto {
  width: 100%;
  height: auto;
}

#box3 {
  background-color: rgb(214, 231, 223);
  width: 100%;
  height: 80vh;
  justify-content: space-evenly;
}

#services {
  margin: 0px;
  color: #538d72;
  font-weight: 500;
}

.servicesRow {
  display: flex;
  width: 100%;
  justify-content: space-evenly;
}

.serviceBlock {
  width: 200px;
}

.serviceBlockTitle {
  color: #538d72;
  font-style: italic;
}

.serviceDescription {
  text-align: center;
}

#learnMoreButton {
  border: none;
  outline: none;
  background-color: #538d72;
  color: white;
  font-size: 10px;
  padding: 14px 28px;
  cursor: pointer;
  font-size: 12px;
}

#learnMoreButton:hover {
  background-color: #7fa895;
}

#box4 {
  width: 100%;
  height: 65vh;
  background-color: white;
  justify-content: center;
}

#playlistContainer {
  display: flex;
  justify-content: center;
  width: 100%;
}

#playlistDescriptionContainer {
  width: 36%;
  height: 420px;
  background-color: rgb(214, 231, 223);
  justify-content: center;
  text-align: center;
}

#playlistDescription {
  font-weight: 400;
  margin: 0px 30px;
  line-height: 45px;
  font-size: 28px;
}

#box5 {
  width: 100%;
  height: 40vh;
  background-color: rgb(214, 231, 223);
  justify-content: center;
}

#talk {
  font-weight: 400;
}

#consultation2 {
  font-style: italic;
  font-weight: 400;
  /* margin: 19px 0px 0px 0px; */
}

#consultationButton {
  border: none;
  outline: none;
  background-color: #538d72;
  color: white;
  font-size: 10px;
  padding: 16px 28px;
  cursor: pointer;
  font-size: 12px;
  margin: 15px 0px 0px 0px;
}

#consultationButton:hover {
  background-color: #7fa895;
}

#box6 {
  background-color: white;
  width: 100%;
  height: 15vh;
  justify-content: center;
}

#footer {
  display: flex;
  justify-content: space-evenly;
  width: 60%;
}

.footerIcon {
  height: 30px;
  width: auto;
  cursor: pointer;
}

.footerIcon:hover {
  transform: scale(1.05);
}
